import React from 'react'
import { leftImgItem, projectsList, rightImgItem } from '../../DataProviders/Projects';
import { SectionTitle } from '../SharedElements';
import ProjectItem from './ProjectItem';

function ProjectsProvider() {
    const projects = projectsList.map((project, index) => {
        // <ProjectItem {...project} />
        if (index % 2 === 0) {
            return <ProjectItem {...project} {...rightImgItem} />
        } else {
            return <ProjectItem {...project} {...leftImgItem}/>
        }
    }
    );

    return (
        <>{projects}</>
    );
}

const Projects = () => {
    return (
        <>
        <SectionTitle>Projects</SectionTitle>
        <ProjectsProvider />
        </>
    )
}

export default Projects
